import cartControl from '../../../components/cartControl/cartControl.vue';
import { cartControlMixin } from '../../../common/mixins.js';
import { mapGetters, mapActions } from 'vuex';

export default {
	components: {
		cartControl
	},
	data() {
		return {
			footerbottom: 0,
			theIndex: null, // 控制滑动效果 当前滑动下标
			oldIndex: null, // 上一个左滑下标
			goodsList: [],
			selectedList: [],
			isAllSelected: false,
			sumPrice: '0.00',
			cartSum: true,
			shopInfo: {
				name:"汉堡王（北京珠江摩尔国际店21157）",
				float_minimum_order_amount: 20,
				order_lead_time: 41,
				delivery_mode: {
					text: "蜂鸟专送"
				},
				float_delivery_fee: 5.5
			}
		};
	},
	watch: {
		watchOption() {
			uni.getStorage({
				key: 'goodsList',
				success: res => {
					this.goodsList = res.data;
					console.log(this.goodsList);
				}
			});
		}
	},
	computed: {
		...mapGetters(['watchOption'])
	},
	mixins: [cartControlMixin],
	methods: {
		handleCheckbox(item) {
			// 单选
			// console.log(item);
			item.selected = !item.selected;

			// 条件: 数组中是否包含该元素
			let isExist = this.selectedList.indexOf(item);
			if (isExist > -1) {
				this.selectedList.splice(isExist, 1);
			} else {
				// push 数组里
				this.selectedList.push(item);
			}

			// 全选状态
			if (this.selectedList.length == this.goodsList.length) {
				this.isAllSelected = true;
			} else {
				this.isAllSelected = false;
			}

			this.sum();
		},
		handleSelectedAll() {
			// 全选
			this.isAllSelected = !this.isAllSelected;

			// 数据处理
			let arr = [];
			this.goodsList.forEach((item, i) => {
				item.selected = this.isAllSelected;
				arr.push(item);
			});

			this.selectedList = this.isAllSelected ? arr : [];

			// 调用合计
			this.sum();
		},
		sum() {
			// 合计
			console.log(this.goodsList);
			this.sumPrice = 0;
			// console.log(this.goodsList)
			this.goodsList.forEach((item, i) => {
				if (item.selected) {
					this.sumPrice = this.sumPrice + item.food_count * item.food_price;
				}
			});
			this.sumPrice = this.sumPrice.toFixed(2);
		},
		handleSingleDelete(item) {
			// 更新storage
			uni.getStorage({
				key: 'goodsList',
				success: res => {
					let goodsList = res.data;
					// 本地删除选中商品
					goodsList.forEach((goods, index) => {
						if (goods.goods_id == item.goods_id) {
							goodsList.splice(index, 1);
						}
					});
					uni.setStorageSync('goodsList', goodsList);
				}
			});

			// 更新数组
			this.goodsList.splice(this.goodsList.indexOf(item), 1);
			this.selectedList.splice(this.selectedList.indexOf(item), 1);

			// 全部删除取消全选状态
			this.isAllSelected = this.goodsList.length > 0 ? this.isAllSelected : false;
			this.sum();
			this.oldIndex = null;
			this.theIndex = null;
		},
		handleMulDelete() {
			// 循环删除所有选中的商品
			while (this.selectedList.length > 0) {
				this.handleSingleDelete(this.selectedList[0]);
			}

			// 初始化数据
			this.selectedList = [];
			this.isAllSelected = false;
			this.sum();
		},
		handleGoodsInfo(item) {
			uni.navigateTo({
				url: '../../goods/goods?goodsInfo=' + JSON.stringify(item)
			});
		},
		handleTouchStart(index, event) {
			// 多点触控 不触发
			if (event.touches.length > 1) {
				return;
			}

			this.oldIndex = this.theIndex;
			this.theIndex = null;

			// 初始化坐标
			this.initXY = [event.touches[0].pageX, event.touches[0].pageY];
		},
		handleTouchMove(index, event) {
			// 多点触控 不触发
			if (event.touches.length > 1) {
				return;
			}

			// 移动位置
			let moveX = event.touches[0].pageX - this.initXY[0];
			let moveY = event.touches[0].pageY - this.initXY[1];

			// 滑动位置小 不触发
			if (Math.abs(moveX) < 5) {
				return;
			}

			// 竖向滑动 不触发
			if (Math.abs(moveY) > Math.abs(moveX)) {
				return;
			}

			// 左滑
			if (moveX < 0) {
				this.theIndex = index;
			}
		},
		handleTouchEnd(index, event) {},
		...mapActions(['setOrderInfo']),
		handleConfirm() {
			// 结算
			// console.log(this.shopInfo);
			
			if(this.selectedList.length < 1){
				uni.showToast({
					title:"请选择结算的商品",
					icon:"none"
				})
				return;
			}
			
			if (this.sumPrice < this.shopInfo.float_minimum_order_amount) {
				uni.showToast({
					title: "¥" + this.shopInfo.float_minimum_order_amount+"元起送",
					icon:"none"
				})
				return;
			}
			this.setOrderInfo({
				shopInfo: this.shopInfo,
				selectFoods: this.selectedList
			});
			uni.navigateTo({
				url: '../../orders/orders'
			});

			// if(this.selectedList.length < 1){
			// 	uni.showToast({
			// 		title:"请选择结算的商品",
			// 		icon:"none"
			// 	})
			// 	return;
			// }

			// 本地存储
			// uni.setStorage({
			// 	key:"confirmList",
			// 	data: this.selectedList,
			// 	success: () => {
			// 		uni.navigateTo({
			// 			url:"../../order/confirm"
			// 		})
			// 	}
			// })
		},
		getGoodsList() {
			uni.getStorage({
				key: 'goodsList',
				success: res => {
					// 将所有商品的选中状态都设置false
					for (let i = 0; i < res.data.length; i++) {
						res.data[i].selected = false;
					}
					this.goodsList = res.data;
					console.log(this.goodsList);
					// 属性数据的初始化
					this.selectedList = [];
					this.isAllSelected = false;
					this.sumPrice = '0.00';
				}
			});
		}
	},
	onShow() {
		this.getGoodsList();
	},
	onLoad() {
		// 兼容h5下的底部菜单



	}
};
